.movieTrendList {
  
  list-style: none;
  display: grid;
  grid-template-columns: 300px ;
  column-gap: 10px;
  justify-content: space-around;
  row-gap: 15px;
  padding: 0;
}

@media screen and (min-width: 768px) {
  .movieTrendList {
    grid-template-columns: 300px 300px ;
  }

}

@media screen and (min-width: 1048px) {
  .movieTrendList {
    grid-template-columns: 300px 300px 300px 300px;
  }

}

.movieTrendItem {
  margin-bottom: 10px;
  padding: 10px;
  margin: 2px;
  border-bottom: solid 1px black;
}

.movieTrendLink {
  color: black;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.movieTrendItem:hover {
  cursor: pointer;
}
.movieTrendItem:hover .movieTrendLink {
  color: rgb(237, 24, 24);
}


.movieTrend__img{
  width: 150px;
  margin-bottom: 20px;
}

.movieTrend__btn{
  display: block;  
  margin:  50px auto 40px auto;

  appearance: none;
	border: .2em solid rgb(237, 24, 24);
	padding: .85em 1.5em;
	color: rgb(237, 24, 24);
	border-radius: 2em;
	transition: 0.3s;
	height: 40px;

}

.movieTrend__btn:hover,
.movieTrend__btnL:focus,
.movieTrend__btn:active{
cursor: pointer;
background: rgba(219, 24, 24, 0.888);
color: #fff;
}


.movieTrend__upBtn{
  position: fixed;
  right: 20px;
  bottom: 20px;

  width: 40px;
  height: 40px;

  color: red;
  animation-name: example;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-direction: reverse;
}

@keyframes example {
  0%   {color: red;}

  100% {color: rgb(0, 0, 0);}
}